.mytooltiptext {
  visibility: hidden;
  width: auto;
  background-color: #fffbcc;
  color: black;
  text-align: center;
  border-radius: 6px;
  border: 1px solid black;
  padding: 5px;
  padding-bottom: 2px;
  margin-top: 25px;
  position: absolute;
  z-index: 200;
  line-height: 1.5;
}

button:hover .mytooltiptext {
  visibility: visible;
  margin-top: 50px;
}

#toolbar-switch:hover .mytooltiptext {
  right: 10px;
  top: -75px;
}

#virtual_move:hover .mytooltiptext {
  visibility: visible;
  right: 10px;
  top: -20px;
}

#virtual_move > span.mytooltiptext {
  left: -20px;
}

#IDM_Edit:hover .mytooltiptext,
#IDM_Help:hover .mytooltiptext,
#IDM_Exit:hover .mytooltiptext {	
  visibility: visible;
}

