  #tabs-1 .board, #tabs-3 .board {
    width: 575px;
    height: 100%;
    /*border: 1px solid gray;*/
    float: left;
    box-sizing: border-box;
}

#tabs-2 .board {
    width: 100%;
    height: 100%;
    /*border: 1px solid gray;*/
    float: left;
    box-sizing: border-box;
    padding: 42px 3px;
}

#tabs-1 .scroll, #tabs-3 .scroll {
    width: 30px;
    height: 100%;
    float: right;
    border: 1px solid black;
    background: url(data:image/bmp;base64,);
    box-sizing: border-box;
    position: relative;
}
.scroll .up {
    width: 100%;
    height: 29px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100%;
    background-color: #EADCBB;
    cursor: pointer;
}

.scroll .down {
    width: 28px;
    height: 29px;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100%;
    background-color: #EADCBB;
    position: absolute;
    bottom: 0px;
    box-sizing: border-box;
    cursor: pointer;
}

.scroll .tracking {
    width: 100%;
    height: 0px;
    background-size: 100% 100%;
}

.scroll .lev-position {
    width: 100%;
    height: 15px;
    background-color: #EADCBB;
    border: 2px black;
    background-size: 100% 100%;
    position: relative;
    top: -3px;
}

#tabs-1 .scroll .up {
    background-image: url(data:image/png;base64,), url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: inherit;
    background-position: center, -100px 0px;
}

#tabs-1 .scroll .down {
    background-image: url(data:image/png;base64,), url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: inherit;
    background-position: center, -100px 0px;
}

#tabs-1 .scroll .tracking {
    background: url(data:image/png;base64,) no-repeat;
}

#tabs-1 .scroll .lev-position {
    /*background: url("../assets/images/scroll_sokoban__fon.png") no-repeat;*/
    background: url(data:image/png;base64,);
    /*background-size: 100% 100%;*/
    background-position: center;
}

#tabs-3 .scroll .up {
    background: url(data:image/png;base64,) no-repeat;
}

#tabs-3 .scroll .down {
    background: url(data:image/png;base64,) no-repeat;
}

#tabs-3 .scroll .tracking {
}

#tabs-3 .scroll .lev-position {
    /*background: url("../assets/images/thumb.png") no-repeat;*/
    background: url(data:image/png;base64,);
    /*background-size: 100% 100%;*/
    background-position: center;
}
