}

.virtual_move_on {
    /*background: url("../assets/images/move5_30x30.png") no-repeat;
    background-size: cover;
    content: '';*/
    content: url(data:image/png;base64,);
    -webkit-user-select: none;
            user-select: none;
}

.virtual_move_off {
    background: none;
    content: '+';
    -webkit-user-select: none;
            user-select: none;
}
